import React, { memo } from 'react'
import { NavLink } from 'react-router-dom'
import { headerLinks } from '../../common/header-nav-data'
import {
  HeaderContainer,
  HeaderLeft,
  HeaderRight
} from './style'




export default memo(function WyyHeader() {

  const renderNavItem = (item) => {
    if (item.type === 'nav') {
      return <NavLink to={item.link} className='link' exact>
        {item.title}
        <span className='spirit_01 span'></span>
      </NavLink>
    }
    if (item.type === 'link') {
      return <a href={item.link} className='link'>{item.title}</a>
    }
  }

  return (
    <HeaderContainer>
      <div className='container'>
        <HeaderLeft>
          <a href='#/' className='logo spirit_01' />
          <ul className='content'>
            {headerLinks.map(item => {
              return <li className='item'>{renderNavItem(item)}</li>
            })}
          </ul>

        </HeaderLeft>

        <HeaderRight>
          <div className='search'>
            <div className='icon' />
            <input className='input' placeholder='音乐/视频/电台/用户' />
          </div>

          <a className='user'>创作者中心</a>
        </HeaderRight>
      </div>
    </HeaderContainer>
  )
})
