import styled from "styled-components"

// 最大盒子样式
export const HeaderContainer = styled.div`
  display: flex;
  width: 100%;
  height: 70px;
  margin: 0 auto;
  position: relative;
  z-index: 1000;
  height: 70px;
  box-sizing: content-box;
  background: #242424;
  border-bottom: 5px solid #C20C0C;
  .container {
    min-width: 1100px;
    /* width: 1100px; */
    margin: 0 auto;
    display: flex;
  }
`

// 盒子左部样式
export const HeaderLeft = styled.div`
  display: flex;
  .logo {
    display: block;
    background-position: 0 0;
    float: left;
    width: 157px;
    height: 69px;
    padding-right: 20px;
  }

  .content {
    display: flex;
    height: 70px;
    .item {
      height: 100%;
      line-height: 70px;
      color: #ccc;
      font-size: 14px;
      position: relative;
      :hover {
        background: #000000;
        text-align: center;
        color: #fff;
      }
      .link {
        padding: 0 20px;
      }
      .active {
        display: block;
        background: #000000;
        padding: 0 20px;
        height: 100%;
        line-height: 70px;
        color: #FFF;
        font-size: 14px;
      }
      .active .span {
        display: block;
        position: absolute;
        left: 50%;
        top: 64px;
        width: 12px;
        height: 7px;
        margin-left: -6px;
        overflow: hidden;
        background-position: -226px 0;
      }
    }
        
    li:last-of-type {
      ::after {
        content: "";
        display: block;
        position: absolute;
        top: 21px;
        left: 100px;
        width: 28px;
        height: 19px;
        background: url('https://s2.music.126.net/style/web2/img/frame/topbar.png?32476bd7999f0ca50f39eacc56efed81') no-repeat 0 9999px;
        background-position: -190px 0;
      }
      /* display: block;
      position: absolute;
      top: 21px;
      left: 100px;
      width: 28px;
      height: 19px;
      background-position: -190px 0;
      background-image: url('../../static/images/spirit_01.png'); */
    }

  }
`

// 盒子右部样式
export const HeaderRight = styled.div`
  display: flex;
  align-items: center;
  flex: 1;
  .search {
    margin-left: 60px;
    width: 158px;
    height: 32px;
    background: #fff;
    border-radius: 28px;
    position: relative;
    .icon {
      width: 158px;
      height: 32px;
      /* margin-top: 19px; */
      background-position: 0 -99px;
      border-radius: 32px;
      background: url('https://s2.music.126.net/style/web2/img/frame/topbar.png?32476bd7999f0ca50f39eacc56efed81') no-repeat 0 9999px;
      background-color: #FFF;
      /* background: url('../../static/images/spirit_01.png') no-repeat 0 9999px; */
    }
    .input {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 30px;
      width: 115px;
      font-size: 12px;
    }
    .input::placeholder {
      color: #9b9b9b;
      width: 120px;
    }
  }
  .user {
      /* display: block; */
      width: 90px;
      height: 32px;
      margin: 19px 0 0 12px;
      box-sizing: border-box;
      padding-left: 16px;
      border: 1px solid #4F4F4F;
      background-position: 0 -140px;
      line-height: 32px;
      text-align: center;
      color: #ccc;
      border-radius: 20px;
    }
`