import WyyFindIndex from '@/main/find'
import WyyFriendIndex from '@/main/friend'
import WyyMineIndex from '@/main/mine'

const router = [
  {
    path: "/",
    exact: true, // 默认展示页面
    component: WyyFindIndex,
  },

  {
    path: "/mine",
    component: WyyMineIndex,
  },

  {
    path: "/friend",
    component: WyyFriendIndex,
  }
]
export {
  router
}